import React, { FC, PropsWithChildren } from "react";

export type TContainerProps = PropsWithChildren & {
  slots?: {
    content: React.ReactNode[];
  };
};

export const Container: FC<TContainerProps> = ({ slots }) => {
  const handleOnDrop = (event: DragEvent) => {
    if (!event.dataTransfer) {
      return;
      
    }
    event.preventDefault();
    const component = event.dataTransfer.getData("component");

    console.log('dropped: ', component)
  };

  return <div onDrop={handleOnDrop}>{slots?.content || null}</div>;
};
